@use 'utilities/variables' as v;

.board {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
}

.cell {
  background: v.$color-cyan;
  border-radius: 10px;
  border: none;
  box-shadow: inset 0 -8px 0 v.$color-cyan-darkest;
  cursor: pointer;
  min-height: 96px;
  min-width: 96px;

  >img {
    height: 40px;
    pointer-events: none;
    width: 40px;
  }
}